import React, { Component } from 'react'

export class Newsitems extends Component {
  render() {
      let {title,description,imageurl,url}=this.props;
    return (
      <div className='mx-6'>
      <div className="card" style={{width: '18rem'}}>
      <img src={!imageurl ? "https://images.news18.com/ibnlive/uploads/2022/05/1024-x-576-3-165206033916x9.jpg":imageurl} className="card-img-top " alt="..."/>
      <div className="card-body">
        <h5 className="card-title">{title}...</h5>
        <p className="card-text">{description}...</p>
        <a href={url}target="-blank" className="btn btn-sm btn-dark"><strong>Read more</strong></a>
      </div>
    </div>
    </div>
    )
  }
}

export default Newsitems